.skill-card {
  width: max-content;
  padding: 0px 16px;
  height: 120px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin: 0px 6px;
  /* background-color: aquamarine; */
  /* border-radius: 8px; */
}
.skill-icon {
  font-size: 80px;
}
.skill-name{
    font-size: 18px;
}
@media only screen and (max-width: 720px){
    .skill-card{
        width: max-content;
        height: 100px;
    }
    .skill-icon{
        font-size: 60px;
    }
    .skill-name{
        font-size: 15px;
    }
}